<template>
  <div class="home">
    <!-- =====轮播图==== -->
    <div class="swiper_da">
      <van-swipe class="my-swipe" :autoplay="2000" indicator-color="white" :show-indicators="false">
        <van-swipe-item v-for="(item,index) in swiperList" :key="index">
          <div class="home_swipwe">
            <img :src="item.banner_img"/>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
     <!-- ====热门推荐====== -->
    <div class="home_hot_recommend">
        <div class="home_hot_recommend_item" v-for="(item,index) in introduce" :key="index">
          <img :src="item.icon"/>
          <font>{{item.title}}</font>
        </div>
    </div>
   <!-- 下边的内容 -->
    <div class="home_container" v-for="(item,index) in homeList"  :key="index">
      <!-- ======明星讲师==== -->
      <div class="home_teacher" v-if="item.channel_info.type===3">
        <div class="home_name">{{item.channel_info.name}}</div>
        <div class="home_index_da">
          <div v-for="(v,i) in item.list" :key="i" class="index_count" @click="goDetails(v)">
            <div class="home_image">
              <img :src="v.teacher_avatar" />
            </div>
            <div>
              <div class="index_name">{{v.teacher_name}}</div>
              <div class="index_introduction">{{v.introduction}}</div>
            </div>
          </div>
        </div>
      </div>
       <!-- ========热门资讯====== -->
           <div class="home_ming" v-if="item.channel_info.type===6">
        <div class="home_name">{{item.channel_info.name}}</div>

        <div class="home_jing_count">
          <div v-for="(v,i) in item.list" :key="i" class="ming_count">
            <div class="ming_image_name">
              <div class="ming_img">
                <img :src="v.thumb_img" />
              </div>
              <div class="ming_name">
                <div class="ming_teacher_name">{{v.title}}</div>
                <div class="ming_level_name">{{v.click_rate}}</div>
              </div>
            </div>
            <div class="ming_introduction">{{v.description}}</div>
          </div>
        </div>
      </div>
      <!-- ========精品课堂====== -->
      <div class="home_jing"  v-if="item.channel_info.type===1">
        <div class="home_name">{{item.channel_info.name}}</div>
        <div class="home_jing_count">
          <div v-for="(v,i) in item.list" :key="i" @click="toTeacherDetails(v)">
            <div class="jing_title">{{v.title}}</div>
            <div v-for="(el,ind) in v.teachers_list" :key="ind" class="jing_count">
              <div class="jing_img_name">
                <div class="home_image_jing">
                  <img :src="el.teacher_avatar" />
                </div>
                <div>{{el.teacher_name}}</div>
              </div>
            </div>
            <div class="jing_num_price">
              <div class="jing_num">{{v.sales_num}}人已报名</div>
              <div class="jing_mian">{{v.price | moneyFilter}}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- ========明星阵容====== -->
      <div class="home_ming" v-if="item.channel_info.type===4">
        <div class="home_name">{{item.channel_info.name}}</div>

        <div class="home_jing_count">
          <div v-for="(v,i) in item.list" :key="i" class="ming_count">
            <div class="ming_image_name">
              <div class="ming_img">
                <img :src="v.teacher_avatar" />
              </div>
              <div class="ming_name">
                <div class="ming_teacher_name">{{v.teacher_name}}</div>
                <div class="ming_level_name">{{v.level_name}}</div>
              </div>
            </div>
            <div class="ming_introduction">{{v.introduction}}</div>
          </div>
        </div>
      </div>
    </div>  
    <!-- ====模态框====== -->
    <div class="show_block_da" v-show="show">
      <div class="show_block">
        <div class="iconfont icon-cuohao" @click="icon"></div>
        <div class="log_image">
          <img src="https://wap.365msmk.com/img/feiji.decaf161.png" />
        </div>
        <div>
          <p class="show_log">赶紧登陆一下吧</p>
        </div>
        <div>
          <p class="show_log_yu">立即预约一对一辅导</p>
        </div>
        <div class="log_botton" @click="goTologin">立即登录</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      swiperList: [], //轮播图数据存放
      introduce: [
        {title:"特色课",icon:require("../assets/hot_commend1.png")},
        {title:"一对一辅导",icon:require("../assets/hot_commend2.png")},
        {title:"学习日历",icon:require("../assets/hot_commend3.png")},
      ],  //热门推荐
      show: false,   //是否显示或隐藏
      homeList: [], //首页分类列表
      teacherItem:[]
    };
  },
  name: "Home",
  mounted() {
    this.AjaxhomeList();
    this.SwiperBanner()
  },
  methods: {
    //获取的轮播图接口数据
    async SwiperBanner() {
      let { data: res } = await this.$http.get("/api/app/banner");
      // console.log(res.data);
      //把获取的接口数据存放到一个swiperList数组中
      this.swiperList = res.data;
    },
    async AjaxhomeList() {
      //获取的首页列表接口数据
      let { data: res } = await this.$http.get("/api/app/recommend/appIndex");
      console.log(res.data);
       //把获取的接口数据存放到一个homeList数组中
      this.homeList = res.data;
    },
    //明星讲师跳转详情
    goDetails(element) {
      // console.log(v);
        let TokenBy=localStorage.getItem("adminToken")
        // console.log(TokenBy)
        if(TokenBy){
          this.$http.get(`/api/app/teacher/${element.teacher_id}`).then((response)=>{
            // console.log(response.data)
            this.$router.push({
              name:"details",
              query:{
                id:element.teacher_id,
                data:response.data.data
              }
            })
          })
        }else{
          this.show=true
        }
    },
    goTologin(){
      this.$router.push("/mine")
    },
    // 精品课堂跳转详情页
toTeacherDetails(v){
    this.$router.push({
        name:"CourseDetails",
        query:{
          id:v.id
        }
    })
},
    icon() {
      this.show = false;
    },
  },
};
</script>
