<template>
  <div class="course_container">
    <!-- 头部 -->
    <div class="course_top_contaier">
      <div class="course_top_left">特色课</div>
      <div class="course_top_right">
        <img src="../assets/sousuo.png" class="course_top_right_icon" v-on:click="toSearch" alt />
      </div>
    </div>

  <!-- 下拉菜单部分 -->
  <van-dropdown-menu active-color=#eb6100>
  <van-dropdown-item title="分类" ref="item">
    <div class="prent_box">
      <div class="tm_kind">
        <p>{{this.classifyData[0].name}}</p>
        <div class="tmk_item">
          <span v-for="(item,index) in classifyData[0].child" :key="index" 
          @click="gradeIndex=index" 
          :class="gradeIndex===index?'tmk_item_span_active':'tmk_item_span'">
            {{item.name}}
          </span>
        </div>
      </div>
      <div class="tm_kind">
        <p>{{this.classifyData[1].name}}</p>
        <div class="tmk_item">
          <span v-for="(item,index) in classifyData[1].child" :key="index" 
          @click="subjectIndex=index" 
          :class="subjectIndex===index?'tmk_item_span_active':'tmk_item_span'">
            {{item.name}}
          </span>
        </div>
      </div>
      <div class="search_btn">
        <van-button type="default" style="width:46%;height:2.2rem" @click="ResetData">重置</van-button>
        <van-button color="#eb6100" style="width:46%;height:2.2rem" @click="onConfirm">确定</van-button>
      </div>
    </div>
  </van-dropdown-item>
  <!-- 排序列表部分 -->
  <van-dropdown-item title="排序" ref="title">
    <div v-for="(item,index) in sortClassify" 
    :key="index" 
    @click="onSortClassify(index)"
    :class="sortClassIndex===index?'sort_classify_item_active':'sort_classify_item'">
        {{item}}
      </div>
  </van-dropdown-item>
  <van-dropdown-item title="筛选"></van-dropdown-item>
</van-dropdown-menu>
<!-- 主体部分 -->
  <!-- 老师name部分 -->
  <van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad"
>
  <van-cell>
    <div class="index_item_page" :immediate-check="immediatecheck">
     <div class="ii_item"  v-for="(item,index) in List" :key="index" @click="goCourseDetails(item)">
   <p class="ii_title"><font>{{item.title}}</font></p>
   <!-- 课时 -->
   <div class="ii_time">
     <p>{{item.start_play_date | dateFormat}}~{{item.end_play_date |dateFormat}}</p>
     <p>共{{item.total_periods}}课时</p>
   </div>
   <!-- teacher图片 -->
   <div class="ii_teacher" v-for="(ele,index) in item.teachers_list" :key="index">
     <div class="teachder_item">
       <img :src="ele.teacher_avatar" style="width:35%"/>
       <font style="margin:0 0.8rem">{{ele.teacher_name}}</font>
     </div>
   </div>
   <!-- bottom信息 -->
   <div class="ii_info">
     <span>{{item.sales_num}}人报名</span>
     <font :class="item.price==0?'green':'orange'">{{item.price |moneyFilter}}</font>
   </div>
 </div>
 </div>
  </van-cell>
</van-list>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
        classifyData: [
        {
          id: 1,
          name: "年级",
          parent_id: 0,
          child: [
            {
              id: 1,
              name: "初一"
            },
            {
              id: 2,
              name: "初二"
            },
            {
              id: 3,
              name: "初三"
            },
            {
              id: 4,
              name: "高一"
            },
            {
              id: 5,
              name: "高二"
            }
          ]
        },
        {
          id: 2,
          name: "学科",
          parent_id: 0,
          child: [
            {
              id: 7,
              name: "语文"
            },
            {
              id: 8,
              name: "数学"
            },
            {
              id: 9,
              name: "英语"
            },
            {
              id: 12,
              name: "物理"
            },
            {
              id: 13,
              name: "化学"
            }
          ]
        }
      ],
        immediatecheck:false, //=====初始化数据第一屏是否加载
        toggle:false, //=====弹出层隐藏/显示
        gradeIndex:-1, //=====当前班级分类下标
        subjectIndex:-1, //=====当前年级分类下标
        gradeClassify:[], //=====班级分类数据
        subjectClassify:[], //====年级分类数据
        color:false, //====默认颜色
        List:[], //=====存放特色课列表数据
        loading: false, //数据上拉加载
        finished: false, //数据已加载完
        courseInfo:{
          page: 1,  //=====第几页开始获取
          limit: 10, //=====请求数据数量  
          course_type: 0, //=====筛选类型
          classify_id:"", //=====分类id
          order_by: "", //=====排序（0综合排序，1最新，2最热，3价格由低到高，4价格由高到低）
          attr_val_id: "", //=====分类id
          is_vip: 0 //=====是否为vip 默认为0
        },//=====特色课请求接口参数
        sortClassify:["综合排序","最新","最热","价格从高到低","价格从低到高"],//排序选项数据
        sortClassIndex:0 //排序选项当前id
    }
  },
  mounted() {
    this.AjaxClassify()
     this.$http.get("/api/app/courseBasis?page=1&limit=10&").then(response => {
      this.List = response.data.data.list;
     console.log(response.data);})
    // this.AjaxcourseBasis()
  },
  methods: {
    // 点击跳转搜索
    toSearch() {
      this.$router.push("/searchs");
    },
     // 去课程详情
    goCourseDetails(item) {
      console.log(item);
      this.$http.get(`/api/app/teacher/${item.id}`).then(response => {
        console.log(response.data);
        this.$router.push({
          path: "/courseDetails",
          query: {
            id: item.id,
            data: response.data.data
          }
        });
      });
      // this.$router.push("/courseDetails");
    },
    //获取分类筛选接口
    async AjaxClassify(){
         let {data:response}=await this.$http.get("/api/app/courseClassify")
         console.log(response.data)
        if(response.code !=200){
          this.$toast(response.msg)
          return false
        }
     },
     //点击确认按钮隐藏弹出层
      onConfirm() {
        this.$http
        .get(`/api/app/courseBasis?`, {
          params: this.courseInfo
        })
        .then(response => {
          console.log(response.data);
          this.List = response.data.data.list;
        });
        let Array=[]
        if(this.gradeIndex != -1){
          let gradeID=this.classifyData[0].child[this.gradeIndex].id
          Array.push(gradeID)
        }
        if(this.subjectIndex != -1){
          let subjectID=this.classifyData[1].child[this.subjectIndex].id
          Array.push(subjectID)
        }
        // console.log(Array)
        //获取接口数据Id
        this.courseInfo.attr_val_id=Array.join(",")
        console.log(this.courseInfo.attr_val_id)
        //更新筛选后的列表数据
      // console.log(this.classifyData[0].child[this.gradeIndex].id)
      // console.log(this.classifyData[1].child[this.subjectIndex].id)
      this.$refs.item.toggle();
    },
    //点击重置按钮重置数据列表
    ResetData(){
      this.courseInfo.attr_val_id=""
      this.AjaxcourseBasis()
      this.$refs.item.toggle();
    },
    // 排序刷新排序后的数据
    onSortClassify(index){
      this.sortClassIndex=index
      // this.courseInfo.order_by=0
      // this.AjaxcourseBasis()
    },
    //上拉加载下拉刷新
    onLoad() {
      // console.log(12222)
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        this.$http.get(`/api/app/courseBasis?page=${++this.courseInfo.page}&limit=10&`).then((response)=>{
          console.log(response.data)
          if(response.code !=200){
          this.$toast(response.msg)
          return false
        }
        // 正常后的接口渲染
        if(this.courseInfo.page !=1){
          //上拉加载时触发
          this.List=this.List.concat(response.data.list)
        }
        this.loading=true
        
        if(response.data.list.length>=0){
        //  this.List=response.data.list
         this.finished=true
        }
        })
        // 加载状态结束
        // 数据全部加载完成
      }, 3000);
    },  
  },
};
</script>
<style>
/* 排序部分 */
.sort_classify_item
{
  width: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
}
.sort_classify_item_active
{
  width: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  color: #eb6100;
}
.green{
  color: green;
}
.orange{
  color: orangered;
}
.course_container {
  width: 100vw;
  height: 100vh;
}
.course_top_contaier {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-bottom: 1px solid rgb(228, 228, 228);
  background: white;
}
.course_top_left {
  position: absolute;
  left: 45%;
}
.course_top_right_icon {
  width: 25px;
  height: 25px;
}
.prent_box
{
  padding-left: 1rem;
  padding-bottom: 0.5rem;
}
.tm_kind
{
  width: 100%;
  /* height: 9.5rem; */
  display: inline-flex;
  justify-content: center;
  /* align-items: center; */
  flex-wrap: wrap;
  padding-bottom: 1rem;
}
.tm_kind p
{
  width: 100%;
  padding: 0.7rem 0;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
}
.tmk_item
{
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.tmk_item_span{
  display: inline-flex;
  width: 18.5%;
  height: 2.2rem;
  margin: 0 1.3rem 0.4rem 0;
  justify-content: center;
  border-radius: 0.2rem;
  align-items: center;
  background: #f5f5f5;
}
.tmk_item_span_active{
  display: inline-flex;
  width: 18.5%;
  height: 2.2rem;
  margin: 0 1.3rem 0.4rem 0;
  justify-content: center;
  border-radius: 0.2rem;
  align-items: center;
  background:#EBEEFE;
  color: #eb6100;
}
.search_btn
{
  width: 100%;
  /* background: chartreuse; */
  align-items: center;
  justify-content: space-evenly;
  padding: 1.2rem 0;
  display: inline-flex;
  
}
.index_item_page
{ 
  /* width: 100%; */
  padding: 0.5rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: rgb(240, 238, 238);
  flex-wrap: wrap;
}
.ii_item
{
  padding: 0 0.8rem;
  width: 90%;
  background: white;
  margin-bottom: 1rem;
}
.ii_title
{
  width: 100%;
  padding-top: 1rem;
}
.ii_time
{
  width: 100%;
  display: inline-flex;
  color: #666666;
}
/* .ii_time p:nth-child(1){
  flex: 1;
  height: 0.8rem;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 0.3rem;
}
.ii_time p:nth-child(2)
{
  flex: 1;
  border-left: 1px solid grey;
  height: 0.8rem;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
} */
.ii_teacher
{
  width: 100%;
  height: 4rem;
  display: inline-flex;
  align-items: center;
}
.teachder_item
{
  width: 6rem;
  height: 4rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
}
.ii_info
{
  width: 100%;
  height: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}
</style>