<template>
<div class="details">
    <div class="details_header_container">
        <div>
            <van-icon name="arrow-left" @click="gohome"/>
        </div>
        <div>讲师详情</div>
    </div>
    <div class="details_teacher_name">
        <div class="details_teacher_content">
            <div class="details_teacher_content_img">
                <img :src="teacherData.avatar"/>
            </div>
            <div class="details_teacher_content_name">
             <div>
                 <span>{{teacherData.teacher_name}}</span>
                 <span style="color:red">{{teacherData.level_name}}</span>
             </div>
             <div style="color:grey;fontSize:0.8rem">
                 男&nbsp;&nbsp;{{teacherData.teach_age}}教龄
             </div>
            </div>
            <div class="details_taecher_content_attention">
                <div :style="color?'color:orange':'color:#999'" @click="onAttention">{{gz | totext}}</div>
            </div>
        </div>
    </div>
    <div class="details_teacher_info_container">
    <van-tabs  color=orange line-width=1.2rem title-active-color=orange>
     <van-tab title="讲师介绍">
         <div class="details_teacher_info_item">
             <div class="details_teacher_info_top" v-for="(element,index) in teacherInfoItem.attr" :key="index">
                 <div class="details_teacher_info_top_name">
                     {{element.attr_name}}
                 </div>
                 <div class="details_teacher_info_top_value" v-for="(ele,index) in element.attr_value" :key="index">
                     {{ele.attr_value_name}}   
                 </div> 
             </div>  
               <div class="details_teacher_info_top_name">老师简介</div>
          <div class="details_teacher_info_top_value">{{teacherInfoItem.intro}}</div>
         </div>
     </van-tab>
    <van-tab title="主讲课程">
       <div class="course_wrap">
            <div class="course_item_container" v-for="(item,index) in courseList.list" :key="index">
              <div class="course_item_top" style="color:#333333;font-size:16px;">{{item.title}}</div>
              <div style="color:#666666;font-size:12px;">共1课时</div>
              <div class="course_name" v-for="(v,i) in item.teachers_list" :key="i">
                <div
                  style="color:#666666;font-size:12px;#;width:28%;display:flex;align-items:center;justify-content:space-around"
                >
                  <img :src="v.teacher_avatar" alt />
                  <div>{{v.teacher_name}}</div>
                </div>
              </div>
              <div class="course_bottom">
                <div style="color:#666666;margin-left:20px;font-size:12px;">{{item.sales_num}}人报名</div>
                <div
                  style="margin-right:20px;"
                  :class="item.price==0?'green':'orange'"
                >{{item.price|moneyFilter}}</div>
              </div>
            </div>
          </div>
    </van-tab>
    <van-tab title="学员评价">
         <div class="student_container">
              <div class="student_none_container" >
                    <img src="../assets/empty.png" alt="">
                    <p>暂无评论</p>
              </div>
            <div class="student_wrap">
                <div class="student_item">x
                </div>
            </div>
          </div>
    </van-tab>
    </van-tabs>
    </div>
    <div class="details_teacher_cart">
        <div>立即购买</div>
    </div>
</div>
</template>
<script>


export default {
    data(){
        return{
             courseList: [],
             commentList: [],
             teacherInfoItem:[],
             teacherData:this.$route.query.data.teacher?this.$route.query.data.teacher:{},
             attention:"关注",
             gray:false,
             color:true,
             gz:''
        }
    },
    filters: {
    sex(val) {
      if (val == 0) {
        return "男";
      }
    },
     totext(val){
         if(val==1){
            return "已关注"
         }else if(val==2){
            return "关注"
         }
     }
  },
    mounted(){
        this.teacherInfo()
        this.courseData();
        this.commentData();
    },
     created(){
        console.log(this.$route.query.id)
            this.$http.get(`/api/app/teacher/${this.$route.query.id}`).then((res)=>{

                 this.gz=res.data.data.flag
                console.log(res.data.data.flag)
            })
     },
    methods: {
        gohome(){
            this.$router.go(-1)
        },
        async teacherInfo(){
            let {data:response}=await this.$http.get(`/api/app/teacher/info/${this.$route.query.id}`)
            // console.log(response.data)
            this.teacherInfoItem=response.data
        },
         async courseData() {
      let { data: res } = await this.$http.post("/api/app/teacher/mainCourse", {
        teacher_id: this.$route.query.id
      });
      //   console.log(res.data);
      this.courseList = res.data;
    },
    async commentData() {
      // let { data: res } = await this.$http.post("/api/app/teacher/comment", {
      //   teacher_id: Number(this.$route.query.id),
      //   page: 1,
      //   limit: 10
      // });
      // console.log(res);
      // this.commentList = res.data;
    },
    async onAttention(){
            let {data:res}=await this.$http.get(`/api/app/teacher/collect/${this.$route.query.id}`);
            // console.log(this.$route.query.id)
              console.log(res)
               this.gz=res.data.flag
            // console.l og(res.data)
            // if(res.data.flag===2){
            //     this.attention="关注"
            //     Toast("已关注")
            //     this.color=true;
            // }else{
            //     Toast("已取消")
            //     this.attention="已关注"
            //     this.color=false
            //                 }
        }
    },
}
</script>
